import React, { useState } from 'react';
import ReactMapGL, {SVGOverlay} from 'react-map-gl';
import 'mapbox-gl/dist/mapbox-gl.css';

const redraw = ({project}) => {
  const [cx, cy] = project([126.8952203, 37.5141425]);
  return <circle cx={cx} cy={cy} r={10} fill="red" />;
}

const Map = () => {
  const [state, setState] = useState({
    viewport: {
      latitude: 37.5141425,
      longitude: 126.8952203,
      zoom: 17,
      bearing: 30,
      pitch: 0
    }
  })
  const { viewport } = state;
  // return <DeckGL width="100%" height="100%" longitude={126.8952203} latitude={37.5141425} zoom={8} controller={true} layers={[scatterplotLayer]} />
  return (<ReactMapGL
    {...viewport}
    mapboxApiAccessToken="pk.eyJ1IjoiZXVuY2h1cm4iLCJhIjoiY2p3cG52cDUyMDhyMTN6cWlybHRhaDBxayJ9.ebHSzW6LO2mU8gKupfSj5w"
    width="100vw"
    height="100vh"
    mapStyle="mapbox://styles/mapbox/dark-v9"
    onViewportChange={(v) => { setState({ viewport: v }) }}
  >
  <SVGOverlay redraw={redraw} />
  </ReactMapGL>)
}

export default Map;